const app = getApp();
import helper from '../common/helper.js';
import pickerAddress from '../components/wangding-pickerAddress.vue';
export default {
	data() {
		return {
			txt: '选择地址',
			// formNode: [],
			formNode: [],
			array: ['男','女','无'],
			arrayCard: ['有','无'],
			index: 0,
			setData: '',
			options: ''
		};
	},
	components: {
		pickerAddress
	},
	onLoad() {
		
	},
	onShow() {
		this.getformNode();
	},
	methods: {
		// 进入编辑页
		goEditSet(e) {
			console.log(e)
			let name = e.currentTarget.dataset.name
			let title = e.currentTarget.dataset.title
			uni.navigateTo({
				url: `/pages/meEditSet?name=${name}&title=${title}`
			})
		},
		bindPickerChange(e){
			console.log(e)
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = e.target.value
			if(e.currentTarget.dataset.name == 'sex'){
				this.setData = this.array[this.index]
			} else{
				this.setData = this.arrayCard[this.index]
			}
			 this.options = e.currentTarget.dataset.name
			 this.submit()
		},
		goAddress(data) {
			console.log(data);
			this.txt = data.data.join('');
			this.formNode.province = data.data[0];
			this.formNode.city = data.data[1];
			this.formNode.district = data.data[2];
			console.log(this.formNode.province, '+');
		},
		getformNode() {
			// 用户信息获取
			uni.showLoading({
				title: '用户信息获取中...'
			});
			uni.request({
				url: `${helper.requestUrl}/me`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					this.formNode = res.data;
				}
			});
		},
		// 修改
		submit() {
			uni.showLoading({
			  title: '提交中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/edit`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					field: this.options,
					value: this.setData
				},
				// data: this.setData,
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == '1') {
						uni.showToast({
							title: res.data.message
						});
						setTimeout(() => {
							this.getformNode()
						}, 2000)
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
			
				}
			})
		}
	}
};
